.Add_holder{
    position: absolute;
    top: 13%;
    width: 30%;
    height: 72.5%;
    left: 35%;
    border-radius: 1em;
    border: .1px solid black;
    background-color: rgb(202, 202, 202);
}
.goBack{
    background: none;
    border: none;
    cursor: pointer;
    width: 70px;
    height: 70px;
}
.addInp{
    background-color: rgb(202, 202, 202);
    border: 0.1px solid black;
    border-radius: 1em;
    margin-left: 2%;
    margin-top: 4%;
    width: 93%;
    height: 7.5%;
    font-size: larger;
    padding-left: 2%;
}
.Add{
    position: inherit;
    background-color: rgb(0, 89, 255);
    border: .1px solid rgb(0, 89, 255);
    border-radius: 5em;
    color: white;
    width: 4em;
    height: 4em;
    top: 60%;
    left: 40%;
}
@media (max-width:1020px) {
    .Add_holder{
        width: 50%;
        left: 25%;
    }
}
@media (max-width:500px) {
    .Add_holder{
        width: 70%;
        left: 15%;
    }
}
@media (max-width:400px) {
    .Add_holder{
        width: 80%;
        left: 10%;
    }
}